<template>
  <div id="myChart" ref="chart" style="width:100%;height:100%" v-resize="redraw"></div>
</template>

<script>
import { getRlhdTotal } from '@/api/dataCenter'
export default {
  name: 'hello',
  data () {
    return {
      myChart: {},
      data: {}
    }
  },
  computed: {
    xAxisData: function () {
      let Arr = []
      for (let item of this.data) {
        if (item.active_type) {
          Arr.push(item.active_type)
        }
      }
      return Arr
    },
    area: function () {
      let Arr = []
      for (let item of this.data) {
        if (item.area) {
          Arr.push(item.area.toFixed(2))
        }
      }
      return Arr
    }
  },
  mounted () {
    this.getAreas()
  },
  methods: {
    /* 获取数据 */
    getAreas () {
      getRlhdTotal({
        data: {}
      }).then(res => {
        if (res.data.code === '0000') {
          this.data = res.data.data
          this.drawLine()
        }
      })
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = this.$echarts.init(this.$refs.chart)
      // 绘制图表
      this.myChart.setOption({
        title: {
          text: '人类活动解译按类型统计面积',
          left: 35,
          top: 10
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '7%',
          top: '80px',
          containLabel: true
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          },
          formatter: function (params) {
            return params[0].name + '：' + params[0].value + 'km²'
          }
        },
        xAxis: {
          type: 'category',
          data: this.xAxisData,
          axisLabel: {
            interval: 0
          }
        },
        yAxis: [
          {
            type: 'value',
            name: '面积/km²',
            min: 0,
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: '面积',
            type: 'bar',
            z: 3,
            itemStyle: {
              normal: {
                color: '#19ce9b'
              }
            },
            data: this.area,
            barWidth: '10%'
          }
        ]
      })
      // 窗口改变时图表自适应
      window.addEventListener('resize', () => this.myChart.resize(), false)
    },
    redraw () {
      if (this.myChart.id !== undefined) {
        this.myChart.resize()
      }
    }
  }
}
</script>

